/** @jsx jsx */
import { jsx, keyframes } from '@emotion/core';
import Box from '@chakra-ui/core/dist/Box';
import PseudoBox from '@chakra-ui/core/dist/PseudoBox';

function createKeyframes(numberOfElements) {
  return keyframes`
	0% { transform: translateX(0); }
	100% { transform: translateX(${-250 * numberOfElements}px)}
`;
}

const fadeStyle = {
  content: '""',
  height: 92,
  position: 'absolute',
  width: 128,
  zIndex: 2,
  top: 0,
  background: 'linear-gradient(to right,  rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%)',
  pointerEvents: 'none',
};

export default function BackgroundSlider({ children, numberOfElements, duration }) {
  return (
    <PseudoBox
      _before={{
        ...fadeStyle,
        left: 0,
      }}
      _after={{
        ...fadeStyle,
        right: 0,
        transform: 'rotateZ(180deg)',
      }}
      overflow="hidden"
      position="relative"
      w="100%"
    >
      <Box
        display="flex"
        css={{ animation: `${createKeyframes(numberOfElements)} ${duration}s linear infinite` }}
        w={250 * numberOfElements * 2}
      >
        {children}
        {children}
      </Box>
    </PseudoBox>
  );
}

export function SliderItem({ children }) {
  return (
    <PseudoBox
      display="flex"
      alignItems="center"
      justifyContent="center"
      minWidth="250px"
      w="250px"
      height="92px"
      transition="all 0.2s ease"
      css={{ filter: 'grayscale(1)' }}
      opacity="0.75"
      _hover={{ filter: 'none', opacity: 1, transform: 'scale(1.05)' }}
    >
      {children}
    </PseudoBox>
  );
}
